import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { HeaderComponent } from './header/header.component';
import { AboutComponent } from './about/about.component';

import { FooterComponent } from './footer/footer.component';
import { ErrorComponent } from './error/error.component';
import { WhatIDoComponent } from './what-ido/what-ido.component';
import { ProjectsComponent } from './projects/projects.component';
import { ResumeComponent } from './resume/resume.component';
import { AboutMeInfoComponent } from './about/about-me-info/about-me-info.component';
import { ProjectService } from './projects/projectService.service';
import { ProjectInfoComponent } from './projects/project-info/project-info.component';

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    AboutComponent,

    FooterComponent,
    ErrorComponent,
    WhatIDoComponent,
    ProjectsComponent,
    ResumeComponent,
    AboutMeInfoComponent,
    ProjectInfoComponent,
  ],
  imports: [BrowserModule, AppRoutingModule],
  providers: [ProjectService],
  bootstrap: [AppComponent],
})
export class AppModule {}
